import {
  Component, EventEmitter, Input, OnInit, Output,
  OnChanges, Inject, PLATFORM_ID
} from '@angular/core';
import { FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
import { SemMediaService } from '../../sem-media.service';
