export class SemUploadComponent {

  @Output() public doneImage = new EventEmitter<any>();

  public visibleImage: Boolean = false;
  public files: UploadFile[] = [];
  constructor(private zone: NgZone) {}

  public loadedImage() {
    this.visibleImage = true;
  }
  public dropped(event: UploadEvent) {
    this.files = event.files;

    for (const droppedFile of event.files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.doneImage.emit(file);
          /**
          // You could upload it like this:
          const formData = new FormData()
          formData.append('logo', file, relativePath)

          // Headers
          const headers = new HttpHeaders({
            'security-token': 'mytoken'
          })

          this.http.post('https://mybackend.com/api/upload/sanitize-and-save-logo', formData, { headers: headers, responseType: 'blob' })
          .subscribe(data => {
            // Sanitized logo returned from backend
          })
          **/

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
      }
    }
  }
  fileChangeEvent(event) {
    for (const chosenFile of event.target.files) {
      this.doneImage.emit(chosenFile);
    }
  }
  public fileOver(event) {
  }

  public fileLeave(event) {
  }
}
